.header{
    *{
        margin: 0;
        padding: 0;
        
    }
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    border: 1px solid black;

    & .nav-bar>ul{
        width: 300px;
        height: 100%;
        margin-right: 10px;
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        align-items: center;

        & ls{
            padding: 100px;
        }
    }
}
.logo{
    width: 100px;
}

.card{
    width: 150px;
    border: 1px solidb black;
    padding: 10px;

    & img{
        width: 100%;
    }
}

.body{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footer{
    color: white;
    background-color: black;
    text-align: center;
}
